import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function daysOfInventoryOutstandingTtm(tableName: string) {
  const title = "Days Inventory Outstanding (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["daysOfInventoryOutstandingTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Days to sell inventory.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about days inventory outstanding or other operational efficiency metrics?`,
              value:
                info.row.original.financialData.companyKeyMetrics?.data
                  ?.daysOfInventoryOutstandingTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            info.row.original.financialData.companyKeyMetrics?.data?.daysOfInventoryOutstandingTtm
          }
        />
      )
    },
  })
}

export { daysOfInventoryOutstandingTtm }
